/* Responsive design for Mobile devices max-480px and min-320px */
@media only screen and (max-width:480px)and (min-width:320px) {
    .tt {
        position: fixed;
        height: 70px;
        width: 70px;
        border: 2px solid black;
        background-color: #7952b3;
        border-radius: 50px;
        font-size: 20px;
        color: white;
        margin: 345px 50px 0px 290px;
        z-index: 2;
        transition: all 2s;
        box-shadow: 0px 7px 8px 7px red;
    }

    .nte {

        position: fixed;

        background-color: rgba(143, 115, 186, 0.946);
        z-index: 1;
        height: 100vh;
        width: 100vw;
        color: white;
    }

    .active {
        display: block;
    }

    .nte {

        padding: 5px;
        display: flex;
        justify-content: center;
        flex-direction: row;

    }

    .gg {

        width: 50%;
        font-size: 14px;
    }

    a {
        color: black;
    }

    .pages {
        margin-left: 0;
        font-size: 14px;

        width: 50%;
    }

    .description {
        margin-top: 8px;
        font-size: 1.3rem;
        font-weight: bold;
        text-align: left;
        font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    }
span{
    color: white;
}

}

@media screen and (max-width:2000px) and (min-width:1201px){
    .tt{
        display: none;
    }
}