

/* Responsive design for Mobile devices max-480px and min-320px */
@media only screen and (max-width:480px)and (min-width:320px) {

.container{
display: flex;
flex-direction: column;
justify-content: center;
 align-items: center; 
position:sticky;
top:0;
background-color:#7952b3;
max-width: 480px;
}
ul{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 0 5px 0 5px;
    text-align:center;
    max-width:480px;
    align-items: center;
}
ul li{
    /* text-decoration: none !important; */
    list-style-type: none;
    color:black;
    margin:0 0 0 12px;
    font-size: 22px;
}
.points{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
/* .butn input{
   
    width: 180px;
    height: 32px;
    border: 0px;
    border-radius: 5px;
   
} */
ul li:hover{
text-decoration: none;
list-style-type: none;
}
.butn{
    margin-bottom: 8px;
    display: flex;
    justify-content:center;
    align-items: center;
    
}
ul li a:hover{
    text-decoration:none;
}
.fa-bars{
    margin-bottom: 3px;
font-size: 30px;
margin-left: 30px;
}
span{
    text-decoration: none !important;
}
.logo{
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-size: 30px;
    background-color:#7952b3;;
    width:100vw;
    text-align: center;
    color:white;
    
    
}
/* button{
    width: 90px;
    height: 30px;
    margin-left:8px;
    margin-bottom: 3px;
    text-align: center;
    padding: 0px;
    border: 1px solid white;
    border-radius: 5px;
    font-weight: bold;
} */
.active{
    display: block;
}
.lastbutton{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
}

}


/* responsive for small screen and laptops */
@media screen and (max-width:2000px) and (min-width:1201px){
    .container{
        display: flex;
        justify-content: center;
        align-items: center;
        color :black;
        flex-direction: column;
        background-color:#7952b3;
        max-width: 100vw;
        position: sticky;
        top: 0;

    }
    ul{
        display: flex;
        justify-content: center;
        flex-direction: row;
        align-items: center;
        color: black;
    }
    ul li {
        color:black;
        list-style-type: none;
        margin-left: 5rem;
        font-size: 1.5rem;
    }
    ul li i{
        margin:10px;
        
    }
     ul li a:hover {
        text-decoration: none;
    }
    .logo{
        color:white;
        font-size: 2rem;
    }
    .GFG{
        color: black;
    }
